import React from "react";
import Product from "./Product";
//
export default function Main(props) {
  const { products, onAdd } = props; // read products from props
  return (
    <main className="block col-1">
      <h2>ArmsProducts</h2>
      <div id="mainDisplayArea" className="row">
        {/* to render list of items from array use products array and map method of array  */}
        {products.map((product) => (
          <Product key={product.id} product={product} onAdd={onAdd}></Product>
        ))}
      </div>
    </main>
  );
}

// export default Main;
